import React, { Component } from 'react';
import { NavBar, Modal, Button } from 'antd-mobile';
import queryString from 'query-string';
import qs from 'qs';
import axios from 'axios';
import './QRoauth.less';

axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';

let leftClick = false;

class QRoauthDouyin extends Component {
  state = {
    needNavBar: true,
    status: '',
    modal: false,
    modalContent: '',
    sourceIcon: '',
    title: '',
    des: '',
    query: '',
    appInfo: {},
    confirmedUrl: '',
  };

  webviewDestroy = () => {
    window.ToutiaoJSBridge.call('close');
  };

  pageBack = () => {
    window.history.back();
  };

  getUrlParames = () => {
    /* eslint-disable */
    const values = queryString.parse(location.search);
    /* eslint-enable */
    return values;
  };

  getAppInfo = () => {
    window.ToutiaoJSBridge.call('appInfo', {}, (appInfo) => {
      this.setState({ appInfo }, () => {
        this.getTicket(appInfo);
      });
    });
  };

  getTicket = (appInfo) => {
    const self = this;
    window.ToutiaoJSBridge.call(
      'fetch',
      {
        url: 'https://aweme.snssdk.com/passport/open/authorize/ticket/',
        method: 'POST',
      },
      (res) => {
        if (res && res._raw) {
          const ticketInfo = JSON.parse(res._raw);
          if (ticketInfo.data && ticketInfo.data.ticket) {
            const ticket = ticketInfo.data.ticket;
            this.setState({ ticket });
            this.changeQRcodeStatus({ ...appInfo, ticket });
          } else {
            if (ticketInfo.data && ticketInfo.data.error_code === 1) {
              /* eslint-disable */
              const userAgent = navigator.userAgent;
              /* eslint-enable */
              const isiOS = !!userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios终端
              if (isiOS) {
                self.loginBridge();
                return;
              }
              // Android
              const { versionCode } = this.state.appInfo;
              /* eslint-disable */
              if (versionCode && parseInt(versionCode) > 280) {
                /* eslint-enable */
                self.loginBridge();
                return;
              }
              self.openAwemeBridge();
              return;
            }
            console.info('/passport/open/authorize/ticket/ error');
          }
        } else {
          console.error('/passport/open/authorize/ticket/ error');
        }
      },
    );
  };

  loginBridge = () => {
    const self = this;
    window.ToutiaoJSBridge.call('login', {}, (cbParam) => {
      const { success } = cbParam;
      if (success) {
        self.getAppInfo();
      } else {
        /* eslint-disable */
        console.info('not login ');
        /* eslint-enable */
        self.webviewDestroy();
      }
    });
  };

  openAwemeBridge = () => {
    const self = this;
    window.ToutiaoJSBridge.call(
      'openAweme',
      {
        type: 'login',
        args: {},
      },
      () => {
        window.ToutiaoJSBridge.on('H5_nativeEvent', (cbParam) => {
          const { type, args } = cbParam;
          if (
            type &&
            type === 'userLogin' &&
            args &&
            args.code &&
            args.code === 1
          ) {
            self.getAppInfo();
          } else {
            self.webviewDestroy();
          }
        });
      },
    );
  };

  changeQRcodeStatus = (parames) => {
    const urlParames = this.getUrlParames();
    const requestUrl = urlParames.next_url;
    const self = this;
    axios
      .post(requestUrl, qs.stringify({ ...parames, ...urlParames }), {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      })
      .then((res) => {
        const responseData = res.data;
        const { data, message } = responseData;
        if (message && message === 'success') {
          self.setState({
            status: 'success',
            sourceIcon: data.source_icon,
            confirmedUrl: data.confirmed_url,
            ...data.source,
          });
        } else {
          if (data && data.description) {
            self.setState({
              needNavBar: false,
              status: 'error',
              modal: true,
              modalContent: data.description,
            });
          }
        }
      })
      .catch(() => {
        /* eslint-disable */
        console.error('changeQRcodeStatus error');
        /* eslint-enable */
      });
  };

  onClose = () => {
    this.setState({
      modal: false,
    });
    this.webviewDestroy();
  };

  onLeftClick = () => {
    this.onClick(1);
    leftClick = true;
    this.webviewDestroy();
  };

  onClick = (decision) => {
    const urlParames = this.getUrlParames();
    const { appInfo, ticket, confirmedUrl } = this.state;
    if (confirmedUrl) {
      axios
        .post(
          confirmedUrl,
          qs.stringify({ decision, ticket, ...appInfo, ...urlParames }),
          { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } },
        )
        .then((res) => {
          const responseData = res.data;
          const { data, message } = responseData;
          if (message && message === 'success') {
            this.webviewDestroy();
          } else {
            if (data && data.description) {
              this.setState({
                status: 'error',
                modal: true,
                modalContent: data.description,
              });
            }
          }
        })
        .catch(() => {
          /* eslint-disable */
          console.error('confirmedUrl error');
          /* eslint-enable */
        });
    }
  };

  componentDidMount() {
    this.getAppInfo();
  }

  componentWillUnmount() {
    if (!leftClick) {
      this.onClick(1);
    }
  }

  render() {
    const {
      needNavBar,
      modal,
      status,
      modalContent,
      sourceIcon,
      title,
      des,
      query,
    } = this.state;

    return (
      <div className="oauth-content">
        {needNavBar && (
          <NavBar
            className="i-navbar-border"
            mode="dark"
            leftContent={<div className="icon-back" />}
            onLeftClick={this.onLeftClick}
          >
            抖音授权
          </NavBar>
        )}
        {status === 'success' && (
          <div className="i-body">
            <div className="img-box">
              <div className="img-div">
                {sourceIcon && (
                  <img className="img" src={sourceIcon} alt="logo" />
                )}
              </div>
              <div className="img-name">{title}</div>
            </div>
            <div className="body-desc">
              <div className="oauth-desc">{des}</div>
              <div className="oauth-info">{query}</div>
            </div>
            <div style={{ width: '100%' }}>
              <div style={{ padding: '0 0.32rem' }}>
                <Button
                  type="warning"
                  className="check-button"
                  onClick={() => {
                    this.onClick(0);
                  }}
                >
                  确认登录
                </Button>
              </div>
            </div>
          </div>
        )}
        <div className="i-body">
          <Modal
            className="i-modal"
            visible={modal}
            transparent
            maskClosable={false}
            onClose={this.onClose}
            footer={[
              {
                text: '好的',
                onPress: () => {
                  this.onClose();
                },
              },
            ]}
            wrapProps={{ onTouchStart: this.onWrapTouchStart }}
          >
            <div>{modalContent}</div>
          </Modal>
        </div>
      </div>
    );
  }
}

export default QRoauthDouyin;



